import React from 'react'
import { graphql } from 'gatsby'

import Layout from '../components/Layout'
import SEO from '../components/seo'

class AboutPage extends React.Component {
  render() {
    const { data } = this.props
    const {title, author, social, image} = data.site.siteMetadata;

    return (
      <Layout location={this.props.location} title={title}>
        <SEO title="About" />
        <div className='authorimage'>
        <img src={image}  />
        </div>
        <h1 className="post-title f3rem">{author}</h1>
        <div className="social">
            <a href={social.github}><i class="icon-github-squared"></i></a>
            <a href={social.codepen}><i class="icon-codeopen"></i></a>
            <a href={social.linkedin}><i class="icon-linkedin-squared"></i></a>
            <a href={social.twitter}><i class="icon-twitter-squared"></i></a>
            <a href={social.facebook}><i class="icon-facebook-squared"></i></a>
            <a href={"mailto:" + social.mail}><i class="icon-mail-alt"></i></a>
        </div>
        <div className="post-line"></div>

        <div className="abouttext">
        <p>Not a quite interesting story. </p>
        <p> Works as a developer for 3 years. Have experience in various web application development cycles. Angular 1.X, Angular, Node Js are my favorite to code. </p>
        <p> Born and Brought up in Srivilliputtur, Tamil Nadu. Currently living near Chennai.</p>
        <p> Previously worked on Sports Enterprise solutions domain. Currently with Customer relationships domain. </p>
        <p> Cricket fanatic. </p>
        <p>  Stay Hungry ! Stay Foolish !  </p>
 

        </div>
      </Layout>
    )
  }
}

export default AboutPage

export const AboutQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        author
        description
        siteUrl
        image
        social {
            twitter
            facebook
            github
            codepen
            linkedin
            mail
        }
      } 
    }
  }
`
